
import AutoComplete from "@/components/common/AutoComplete.vue";
import CustomSelect from "@/components/common/CustomSelect.vue";
import DatePicker from "@/components/common/DatePicker.vue";
import TimePicker from "@/components/common/timePicker/TimePicker.vue";
import Dialog from "@/components/dialog";
import AnalysisChart from "@/components/FlexSchedule/AnalysisChart.vue";
import CostAnalysisDialog from "@/components/FlexSchedule/CostAnalysisDialog.vue";
import DayCell from "@/components/FlexSchedule/DayCell.vue";
import DraftCreationDialog from "@/components/FlexSchedule/DraftCreationDialog.vue";
import EditFlexSchedule from "@/components/FlexSchedule/EditFlexSchedule.vue";
import ExtraEmpDetail from "@/components/FlexSchedule/ExtraEmpDetail.vue";
import FlexScheduleCellControl from "@/components/FlexSchedule/FlexScheduleCellControl.vue";
import FlexScheduleCostCenter from "@/components/FlexSchedule/FlexScheduleCostCenter.vue";
import {CFRemoteData} from "@/components/FlexSchedule/FlexScheduleInterface";
import PrintableView from "@/components/FlexSchedule/Print/PrintableView.vue";
import PublishDialog from "@/components/FlexSchedule/PublishDialog.vue";
import SetupGridData from "@/components/FlexSchedule/setupGridData";
import {Leave} from "@/remote/flexSchedulesRequest";
import Remote from "@/remote/scheduleRequest";
import {
    EmployeeActions,
    EmployeeGetters,
    ManagedEmployee,
} from "@/store/employeeModule";
import {
    FlexScheduleAction, FlexScheduleCellSetting,
    FlexScheduleMutation, SortOptions,
} from "@/store/flexScheduleModule";
import {LookupActions} from "@/store/lookupModule";
import {Icon} from "@iconify/vue";
import moment, {Moment} from "moment";
import {defineComponent, ref, inject } from "vue";
import {useRouter} from "vue-router";
import {ScheduleDay} from "./FlexScheduleInterface";
import _ from "lodash";
import {isMobileV2, isScreenTablet} from "@/components/common/script";
import FlexScheduleMultiColumn from "@/components/FlexSchedule/FlexScheduleMultiColumn.vue";
import {useStore} from "vuex";
import { CompanyConfigInterface } from "@/plugins/companyConfig";

declare var Affinity: any;

export enum WeeklyPublishStatus {
    empty = "",
    draft = "draft",
    published = "published",
    partial = "partial",
}

export default defineComponent({
    name: "FlexSchedule",
    components: {
        FlexScheduleCostCenter,
        CostAnalysisDialog,
        AnalysisChart,
        PrintableView,
        DraftCreationDialog,
        DatePicker,
        TimePicker,
        EditFlexSchedule,
        DayCell,
        FlexScheduleCellControl,
        Icon,
        AutoComplete,
        ExtraEmpDetail,
        PublishDialog,
        CustomSelect,
        FlexScheduleMultiColumn
    },
    data() {
        return {
            dayFocus: moment(),
            weekDays: <any[]>[], // calendar week days name
            // scheduleWeek: <any[]>[], // list of data per week
            datePickerTop: 0,
            datePickerLeft: 0,
            employeeList: <any>[],
            employeeHeightArr: <any>[],
            scheduleWeekMap: new Map(),
            weekStatus: WeeklyPublishStatus.empty,

            //
            showControl: false,
            extraEmpDetail: [],
            isAdmin: false,
            hasFuturePaidDate: false,

            //
            costCenterOptions: <any>[],
            selectableColumns: <SortOptions[]>[],
            gridColumnsStyle: {},
            tableMinWidthStyle: {},

            // inject
            manageCompanyConfig: <any>null,

        };
    },

    setup() {
        const router = useRouter();

        if (isMobileV2()) {
            router.push({
                name: "FlexScheduleMobile",
            });
        }
        // const store = useStore()
        // store.dispatch(FlexScheduleAction.loadTargetSchedule,6)
        const DraftCreationDialogRef =
            ref<InstanceType<typeof DraftCreationDialog>>();
        const EditModalRef = ref<InstanceType<typeof EditFlexSchedule>>();
        const ExtraEmpDetailRef = ref<InstanceType<typeof ExtraEmpDetail>>();
        const PrintableViewRef = ref<InstanceType<typeof PrintableView>>();
        const AnalysisChartRef = ref<InstanceType<typeof AnalysisChart>>();
        const PublishDialogRef = ref<InstanceType<typeof PublishDialog>>();
        const CostAnalysisDialogRef =
            ref<InstanceType<typeof CostAnalysisDialog>>();

        const isEditingDialog = ref(false);
        const externalDetail = ref(<CFRemoteData[]>[]);
        const TableContentsRef = ref(<any>[]);
        const TableNameRef = ref(<any>{});
        const store = useStore()

        const cellSettings: FlexScheduleCellSetting = store.state.FlexSchedule.flexScheduleCellSetting

        function openCostAnalysis() {
            CostAnalysisDialogRef.value!.open();
        }

        function openDraftDialog() {
            DraftCreationDialogRef.value!.toggleShow();
        }

        function openAnalysisChart() {
            AnalysisChartRef.value!.toggleShow();
        }

        function openPublishDialog() {
            PublishDialogRef.value!.toggleShow();
        }

        function openEditDialog(isEdit: false) {
            isEditingDialog.value = isEdit;
            EditModalRef.value!.openEdit();
        }

        function openEmployeeDetail(employeeNo: number) {
            Dialog.loading();
            Remote.GetEmployeeExtraDetail(employeeNo).then(
                (res: CFRemoteData[]) => {
                    externalDetail.value = res.map((data: CFRemoteData) => {
                        if (data.InputType === "DateTime") {
                            data.remoteKeyValue = data.KeyValue;
                            data.remoteValue = data.Value;
                            data.KeyValue = moment(data.KeyValue).format(
                                "ddd DD MMM YYYY"
                            );
                            data.Value = moment(data.Value).format(
                                "ddd DD MMM YYYY"
                            );
                        } else if (data.InputType === "Double") {
                            data.remoteKeyValue = data.KeyValue;
                            data.remoteValue = data.Value;
                            data.KeyValue =
                                Math.round(data.KeyValue * 100) / 100;
                            data.Value = Math.round(data.Value * 100) / 100;
                        }
                        return data;
                    });
                    Dialog.clear();
                    ExtraEmpDetailRef.value!.toggleShow();
                },
                (e) => {
                    Dialog.clear();
                    Dialog.alert("Something went wrong");
                }
            );
        }

        function printFlexSchedule() {
            PrintableViewRef.value!.printFlexSchedule();
        }

        return {
            openDraftDialog,
            openEditDialog,
            openEmployeeDetail,
            openAnalysisChart,
            printFlexSchedule,
            openPublishDialog,
            openCostAnalysis,
            DraftCreationDialogRef,
            EditModalRef,
            ExtraEmpDetailRef,
            PrintableViewRef,
            AnalysisChartRef,
            PublishDialogRef,
            CostAnalysisDialogRef,
            TableContentsRef,
            TableNameRef,
            isEditingDialog,
            externalDetail,
            cellSettings,
        };
    },
    async mounted() {
        this.manageCompanyConfig = inject('MANAGE_COMPANY_CONFIG') as CompanyConfigInterface;

        this.$store.dispatch(
            LookupActions.getPositions,
            Affinity.user.EmployeeNo
        );
        this.$store.dispatch(LookupActions.getShifts);
        this.$store.dispatch(LookupActions.getCostElement);
        await this.$store.dispatch(LookupActions.getWorkTypes);

        let weekStart
        let weekEnd
        if (this.manageCompanyConfig.getAllowSundayFirst()) {
            weekStart = this.dayFocus.clone().startOf("week");
            weekEnd = this.dayFocus.clone().endOf("week");    
        } else {
            weekStart = this.dayFocus.clone().startOf("isoWeek");
            weekEnd = this.dayFocus.clone().endOf("isoWeek");    
        }
        
        this.loadDate(weekStart, weekEnd);

        Remote.GetDefaultWorkTypes().then((res) => {
            // note default worktypes is a array
            this.$store.state.FlexSchedule.defaultWorkTypes = res;
        });

        if (Affinity.user.EmployeeNo >= 5000000) {
            this.isAdmin = true;
        }

        this.selectableColumns = this.$store.state.FlexSchedule.selectableColumn;

        this.multiColumnSelected();
    },

    methods: {
        multiColumnSelected() {
            this.selectableColumns = this.$store.state.FlexSchedule.selectableColumn
            let columnStyle = '120px ';
            for (const columnOption of this.selectableColumns) {
                if (columnOption.selected) {
                    columnStyle = columnStyle + ' ' + columnOption.width + 'px'
                }
            }
            columnStyle = columnStyle + ' repeat(7, 1fr)';
            this.gridColumnsStyle = {'grid-template-columns': columnStyle}
            this.calculateTableMinWidth()
            this.sortWeekScheduleData();
        },

        calculateTableMinWidth() {
            this.selectableColumns = this.$store.state.FlexSchedule.selectableColumn
            let minWidth = 120 ;

            for (const columnOption of this.selectableColumns) {
                if (columnOption.selected) {
                    minWidth = minWidth + columnOption.width
                }
            }
            minWidth = minWidth + (110 * 7);
            const minWidthStyle = minWidth + 'px';
            this.tableMinWidthStyle = {'min-width': minWidthStyle}
        },

        sortWeekScheduleData() {
            // Notes: multicolumn sort removed. Only one sort active at a time.
            const sortOption = this.$store.state.FlexSchedule.selectableColumn;
            const values = Array.from(this.scheduleWeekMap.values());
            let result
            const selectedOptions = sortOption.filter((s: any) => {
                return s.selected && !!s.sort;
            })
            if (selectedOptions && selectedOptions.length > 0) {
                const sOptionName = this.getOptionNameKey(selectedOptions[0].optionName);

                const arrWithValue = values.filter((v: any) => {
                    return v[sOptionName] !== null && v[sOptionName] !== undefined
                })
                // filter empty values
                const emptyValues = values.filter((v: any) => {
                    return v[sOptionName] === null || v[sOptionName] === undefined
                })

                // build for loop for multi sort
                let iteratee = []
                let orders: (boolean | "asc" | "desc")[] = []
                for (let option of sortOption) {
                    if (option.selected && option.sort !== '') {
                        iteratee.push(this.getOptionNameKey(option.optionName))
                        if (option.sort === 'Ascend') {
                            orders.push('asc')
                        } else if (option.sort === 'Descend') {
                            orders.push('desc')
                        }

                    }
                }

                if (iteratee.length === 0) {
                    // result = values.sort((a, b) => (a.employeeName > b.employeeName) ? 1 : -1)
                    result = _.orderBy(values,  o => o.employeeName.toLowerCase(), ['asc']);
                } else {
                    result = _.orderBy(arrWithValue, iteratee, orders);
                    if (emptyValues && emptyValues.length > 0) {
                        result = [...result, ...emptyValues]
                    }
                }

            } else {
                result = _.orderBy(values,  o => o.employeeName.toLowerCase(), ['asc']);
            }




            const scheduleWeekmap = new Map();
            result.map((v: any ) => {
                scheduleWeekmap.set(v.employeeNo, v)
            })
            this.scheduleWeekMap = this.$store.state.FlexSchedule.scheduleWeek = scheduleWeekmap;
            this.$nextTick(() => {
                this.resetEmployeeHeight();
            })
        },

        getOptionNameKey(optionName: string) {
            switch(optionName) {
                case 'Std Hours':
                    return 'stdHoursPerWeek'
                case 'Net Hours':
                    return 'netHours'
                case 'Worker Type':
                    return 'workTypeName'
                case 'Ta Role':
                    return 'taRole'
                case 'Roster Group':
                    return 'defaultRosterGroup'
                default: return ''
            }
        },

        showCopy() {
            if (this.hasFuturePaidDate) {
                return false;
            }
            return moment().diff(this.dayFocus.endOf("week"), "day") <= 0;
        },
        async reloadAfterEdit() {
            let weekStart
            let weekEnd
            if (this.manageCompanyConfig.getAllowSundayFirst()) {
                weekStart = this.dayFocus.clone().startOf("week");
                weekEnd = this.dayFocus.clone().endOf("week");    
            } else {
                weekStart = this.dayFocus.clone().startOf("isoWeek");
                weekEnd = this.dayFocus.clone().endOf("isoWeek");    
            }
            await this.refreshScheduleData(weekStart, weekEnd);
            this.multiColumnSelected();
        },

        getCurrentWeekStatus() {
            if (
                this.weekStatus === WeeklyPublishStatus.empty ||
                this.weekStatus === WeeklyPublishStatus.draft
            ) {
                return "Draft";
            } else if (this.weekStatus === WeeklyPublishStatus.partial) {
                return "Partially Published";
            } else if (this.weekStatus === WeeklyPublishStatus.published) {
                return "Published";
            }

            return this.weekStatus;
        },

        async loadDate(weekStart: Moment, weekEnd: Moment) {
            this.weekStatus = WeeklyPublishStatus.empty;
            this.showControl = false;
            this.hasFuturePaidDate = false;

            let param = {
                managerEmployeeNo: Affinity.user.EmployeeNo,
                dateFrom: weekStart.format("YYYY-MM-DD"), // dateFrom: '2020-01-01',
                dateTo: weekEnd.format("YYYY-MM-DD"), // dateTo: '2021-01-01',
            };

            Dialog.loading();
            let [dateData, leaves, employeeList] = await Promise.all([
                this.$store.dispatch(FlexScheduleAction.loadFlexiSchedules, {
                    startDate: weekStart,
                    endDate: weekEnd,
                }),
                Remote.GetManagedLeaves(param),
                this.$store.dispatch(
                    EmployeeActions.getEmployees,
                    Affinity.user.EmployeeNo
                ),
            ]).finally(() => {
                Dialog.clear();
            });
            Dialog.clear();
            this.setupGridData(dateData, leaves, employeeList, weekStart);
            this.$store.state.FlexSchedule.leaveList = leaves;

            this.setupDate(weekStart);
            this.checkStatus(this.scheduleWeekMap);
            this.setupCostCenterList(employeeList);
            this.sortWeekScheduleData();

            this.$nextTick(() => {
                this.resetEmployeeHeight();
            })
        },

        changeCellControl() {
            // add timeout to wait until table body finish render.
            // can be rewrite into ResizeObserver
            setTimeout(() => {
                this.resetEmployeeHeight()
            }, 200)

        },
        resetEmployeeHeight() {
            let index = 0;
            for (const child of this.TableContentsRef.children) {
                this.employeeHeightArr[index] = Math.round(child.getBoundingClientRect().height * 100) / 100;
                index++ ;
            }
        },

        async refreshScheduleData(weekStart: Moment, weekEnd: Moment) {
            this.weekStatus = WeeklyPublishStatus.empty;
            this.showControl = false;
            this.hasFuturePaidDate = false;

            Dialog.loading();
            const dateData = await this.$store.dispatch(
                FlexScheduleAction.loadFlexiSchedules,
                {startDate: weekStart, endDate: weekEnd}
            );

            Dialog.clear();
            this.setupGridData(
                dateData,
                this.$store.state.FlexSchedule.leaveList,
                this.$store.getters[EmployeeGetters.getEmployees],
                weekStart
            );
            this.checkStatus(this.scheduleWeekMap);

            for (const [key, employeeData] of this.scheduleWeekMap.entries()) {
                employeeData.show = false;
            }
            for (const employee of this.$store.state.EmployeeModule
                .filteredEmployees) {
                const employeeWeek = this.scheduleWeekMap.get(
                    employee.employeeNo
                );
                employeeWeek.show = true;
            }

            this.$nextTick(() => {
                console.log(" refreshScheduleData For loop is all done rendering everything!");
                this.resetEmployeeHeight();
            })
        },

        setupGridData(
            dateData: any,
            leaves: Leave[],
            employeeList: ManagedEmployee[],
            weekStart: Moment
        ) {
            let gridDataSet: Map<string, any> = SetupGridData.prepareGridData(
                weekStart,
                employeeList,
                dateData,
                leaves
            );
            SetupGridData.scheDaysToColumn(weekStart, gridDataSet);
            SetupGridData.addLeaveToGrid(weekStart, leaves, gridDataSet);
            SetupGridData.addUnAvaToGrid(dateData, gridDataSet);
            this.scheduleWeekMap = this.$store.state.FlexSchedule.scheduleWeek = gridDataSet;
        },

        setupDate(weekStart: Moment) {
            this.$store.state.FlexSchedule.calendarFocusDate = this.dayFocus;
            this.weekDays = this.getSelectedWeek(weekStart); // todo: check this move to top
        },

        setupCostCenterList(employeeList: ManagedEmployee[]) {
            const costCenterIdsDuplicate = employeeList.map((e) => e.cost1);
            const costCenterIds = [...new Set(costCenterIdsDuplicate)];

            this.$store
                .dispatch(LookupActions.getCostCenters, costCenterIds)
                .then(() => {
                    this.costCenterOptions = this.$store.state.LookUp.costCenters;
                    this.costCenterOptions = this.costCenterOptions.map(
                        (option: any) => {
                            option.selected = false;
                            return option;
                        }
                    );

                    this.costCenterOptions.unshift({
                        costCentreId: -1,
                        description: "All",
                        selected: true,
                    });
                });
        },

        selectCostCenter(selectedCostCentres: any) {
            const selected = this.costCenterOptions.filter((option: any) => {
                return option.selected;
            });

            const selectedIds = selectedCostCentres.map((c: any) => {
                return c.selected ? c.costCentreId : false;
            });

            if (!!selectedIds.find((e: any) => e === -1)) {
                for (const [key, employeeData] of this.scheduleWeekMap.entries()) {
                    employeeData.show = true;
                }
                this.$store.state.EmployeeModule.filteredEmployees = this.$store.state.EmployeeModule.employees;
                return;
            }

            let newEmployeeList = this.$store.state.EmployeeModule.employees.filter(
                    (e: ManagedEmployee) => {
                        return !!selectedIds.find((id: any) => id === e.cost1);
                    }
                );
            this.$store.state.EmployeeModule.filteredEmployees =
                newEmployeeList;

            for (const [key, employeeData] of this.scheduleWeekMap.entries()) {
                employeeData.show = false;
            }

            for (const employee of newEmployeeList) {
                const employeeWeek = this.scheduleWeekMap.get(
                    employee.employeeNo
                );
                employeeWeek.show = true;
            }
        },

        checkStatus(scheduleWeekMap: any) {
            let statusSet = new Set();

            for (const employeeSchedule of scheduleWeekMap.values()) {
                employeeSchedule.empSchePublishState = "";
                for (const sche of employeeSchedule.sche) {
                    if (!sche.publishStatus) {
                        // console.log('sche', sche)
                        // if (!statusSet.has(WeeklyPublishStatus.empty)) statusSet.add(WeeklyPublishStatus.empty)
                    } else if (
                        sche.publishStatus === WeeklyPublishStatus.draft
                    ) {
                        // if (!statusSet.has(WeeklyPublishStatus.draft)) statusSet.add(WeeklyPublishStatus.draft)
                        employeeSchedule.empSchePublishState =
                            WeeklyPublishStatus.draft;
                    } else if (
                        sche.publishStatus === WeeklyPublishStatus.published
                    ) {
                        // if (!statusSet.has(WeeklyPublishStatus.published)) statusSet.add(WeeklyPublishStatus.published)
                        employeeSchedule.empSchePublishState =
                            WeeklyPublishStatus.published;
                    }

                    // check schedule has Future Paid Date in current week
                    if (
                        !this.hasFuturePaidDate &&
                        sche.isPaid &&
                        moment(0, "HH").diff(moment(sche.date), "days") <= -1
                    ) {
                        this.hasFuturePaidDate = true;
                    }
                }

                if (employeeSchedule.empSchePublishState === "") {
                    statusSet.add(WeeklyPublishStatus.empty);
                } else {
                    statusSet.add(employeeSchedule.empSchePublishState);
                }

                // check status if employee has a Team or Personal Schedule
                let includeTeamOrPersonalSche = employeeSchedule.sche.find(
                    (sche: any) => {
                        return (
                            sche.scheduleType === "Personal" ||
                            sche.scheduleType === "Team"
                        );
                    }
                );

                if (includeTeamOrPersonalSche) {
                    employeeSchedule.includeTeamOrPersonal = true;
                }
            }

            // if no published show draft
            if (!statusSet.has(WeeklyPublishStatus.published)) {
                this.weekStatus = WeeklyPublishStatus.draft;
            } else {
                // if has publish
                // and has draft or empty show partial
                if (
                    statusSet.has(WeeklyPublishStatus.draft) &&
                    statusSet.has(WeeklyPublishStatus.published)
                ) {
                    this.weekStatus = WeeklyPublishStatus.partial;
                } else if (
                    statusSet.has(WeeklyPublishStatus.empty) &&
                    statusSet.has(WeeklyPublishStatus.published)
                ) {
                    this.weekStatus = WeeklyPublishStatus.partial;
                } else {
                    // if has publish but no draft or empty show published
                    this.weekStatus = WeeklyPublishStatus.published;
                }
            }
        },

        async chooseCalendarDate($event: any) {
            let element = $event.target.getClientRects()[0];
            this.datePickerTop = element.bottom;
            this.datePickerLeft = element.left;
            let datePickerResult;
            try {
                datePickerResult = await (
                    this.$refs.DatePickerRef as any
                ).toggleShow(this.dayFocus);
            } catch (e) {
                return;
            }
            await this.selectDate(datePickerResult);
        },

        async selectDate(date: Moment) {
            this.dayFocus = date;
            this.$store.state.FlexSchedule.calendarFocusDate = date;

            let weekStart
            let weekEnd
            if (this.manageCompanyConfig.getAllowSundayFirst()) {
                weekStart = date.clone().startOf("week");
                weekEnd = date.clone().endOf("week");    
            } else {
                weekStart = date.clone().startOf("isoWeek");
                weekEnd = date.clone().endOf("isoWeek");    
            }


            // let weekStart = date.clone().startOf("week");
            // let weekEnd = date.clone().endOf("week");
            // return this.getDateData(weekStart, weekEnd)
            await this.loadDate(weekStart, weekEnd);
        },
        getSelectedWeek(selectedDate: any) : { momentDay: Moment; day: string }[] {
            let weekStart: Moment;
            let weekEnd: Moment;
            if ((this.manageCompanyConfig as any).getAllowSundayFirst()) {
                weekStart = selectedDate.clone().startOf("week");
                weekEnd = selectedDate.clone().endOf("week");
            } else {
                weekStart = selectedDate.clone().startOf("isoWeek");
                weekEnd = selectedDate.clone().endOf("isoWeek");
            }
            
            let weekDays = [];

            for (let i = 0; i <= 6; i++) {
                let momentDay = moment(weekStart).add(i, "days");
                weekDays.push({
                    momentDay: momentDay,
                    day: momentDay.format("ddd, MMM Do"),
                });
            }

            return weekDays;
        },

        getLastWeek() {
            this.dayFocus.subtract(7, "days");

            let weekStart: Moment;
            let weekEnd: Moment;
            if ((this.manageCompanyConfig as any).getAllowSundayFirst()) {
                weekStart =  this.dayFocus.clone().startOf("week");
                weekEnd =  this.dayFocus.clone().endOf("week");
            } else {
                weekStart =  this.dayFocus.clone().startOf("isoWeek");
                weekEnd =  this.dayFocus.clone().endOf("isoWeek");
            }
            
            // let weekStart = this.dayFocus.clone().startOf("week");
            // let weekEnd = this.dayFocus.clone().endOf("week");

            this.loadDate(weekStart, weekEnd);
        },
        getNextWeek() {
            this.dayFocus.add(7, "days");
            let weekStart: Moment;
            let weekEnd: Moment;
            if ((this.manageCompanyConfig as any).getAllowSundayFirst()) {
                weekStart =  this.dayFocus.clone().startOf("week");
                weekEnd =  this.dayFocus.clone().endOf("week");
            } else {
                weekStart =  this.dayFocus.clone().startOf("isoWeek");
                weekEnd =  this.dayFocus.clone().endOf("isoWeek");
            }
            this.loadDate(weekStart, weekEnd);
        },

        moreDetail(day: ScheduleDay) {
            this.$store.commit(FlexScheduleMutation.setScheduleDay, day);
        },

        getTotalHour(sche: ScheduleDay[]) {
            let totalHours = 0;
            for (let day of sche) {
                if (day.hours) {
                    totalHours += day.hours;
                }
            }
            return totalHours.toFixed(2);
        },

        confirmEdit(day: any) {
            day.editing = false;
        },

        cancelEdit(day: any) {
        },

        editScheDay(day: any) {
            day.editing = true;
        },

        selectEmployeeAndPublish() {
            this.PublishDialogRef!.setSelectedDate(
                this.dayFocus.week().toString()
            );
            this.openPublishDialog();
        },

        publishCurrentWeek(employees: number[]) {
            // let groupedSche: any[] = [];
            let weekStart: Moment;
            let weekEnd: Moment;
            if ((this.manageCompanyConfig as any).getAllowSundayFirst()) {
                weekStart =  this.dayFocus.clone().startOf("week");
                weekEnd =  this.dayFocus.clone().endOf("week");
            } else {
                weekStart =  this.dayFocus.clone().startOf("isoWeek");
                weekEnd =  this.dayFocus.clone().endOf("isoWeek");
            }

            // let weekStart = this.dayFocus.clone().startOf("week");
            // let weekEnd = this.dayFocus.clone().endOf("week");
            
            // let employees = this.$store.getters[EmployeeGetters.getEmployees].map((e: ManagedEmployee) => e.employeeNo)

            // for (const [key, employeeData] of this.scheduleWeekMap.entries()) {
            //     let scheWithData = employeeData.sche.filter((sche: any) => {
            //         return !!sche.Id
            //     })
            //
            //     groupedSche = groupedSche.concat(scheWithData)
            // }

            let data = {
                EmployeeNumbers: employees,
                DateFrom: weekStart.format("YYYY-MM-DD"),
                DateTo: weekEnd.format("YYYY-MM-DD"),
            };
            Dialog.loading();

            Remote.PublishFlexSchedule(data).then(
                () => {
                    Dialog.clear();
                    this.reloadAfterEdit();
                },
                (e: any) => {
                    if (e.response.status == 408) {
                        Dialog.alert(
                            "Your schedules are being published. This process can take a few minutes. You will need to refresh your screen to see the published schedules."
                        );
                        return;
                    }
                    console.error(e);
                    Dialog.alert("Something went wrong.");
                }
            );
        },

        changeDisplayControl() {
            this.showControl = !this.showControl;
        },
    },
});
